*{
    margin: 0;
    padding: 0;
}

.banner{
    margin-top:-10vh;
    height:80vh;
    margin-bottom: -80px;
    color: white;
	object-fit: contain;
}
.banner_contents{
    margin-left: 30px;
    padding-top: 140px;
    height: 300px;
    /* vertical-align: bottom; */
}
.banner_title{
    font-size: 3rem;
    font-weight: 800;
    padding-bottom: 0.3rem;

    
}
.banner_description{
    line-height: 1.3;
    padding: 1rem 0;
    font-size: 1rem;
    max-width: 360px;
    height: 90px;
    /* vertical-align: text-bottom; */
}
.banner_button{
    
    cursor: pointer;
    color: #fff;
    outline: none;
    border: none;
    font-weight: 700;
    border-radius: 0.2vw;
    padding: 0.5rem 2rem;
    margin-right: 1rem;
    background-color: rgba(51, 51, 51, 0.5);
}
.play {
    color: #000;
    background-color: #fff;
}
.banner_button:hover {
    color: #000;
    background-color: #e6e6e6;
    transition: all 0.2s;
}
.banner_fadeBottom {
    height: 40vh;
    background-image: linear-gradient(
        180deg,
        transparent,
        rgb(37, 37, 37, 0.91),
        #111
        );
}